import * as React from 'react';
import PropTypes from 'prop-types';
import LinearProgress from '@mui/material/LinearProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { useEffect } from 'react';
import { fetchTabData } from '../../config/MyService';

function LinearProgressWithLabel(props) {
    return (
        <Box sx={{ display: 'flex', alignItems: 'center' }} className="skill-mb">
            <Box sx={{ width: '80%', mr: 2 }}>
                <LinearProgress variant="determinate" {...props} />
            </Box>
            <Box sx={{ width: '15%' }}>
                <Typography component={'div'} variant="body2" color="#fff">{props.name}</Typography>
            </Box>
            <Box sx={{ width: '5%' }}>
                <Typography component={'div'} variant="body2" color="#fff">{`${Math.round(
                    props.value,
                )}%`}</Typography>
            </Box>
        </Box>
    );
}

LinearProgressWithLabel.propTypes = {
    /**
     * The value of the progress indicator for the determinate and buffer variants.
     * Value between 0 and 100.
     */
    value: PropTypes.number.isRequired,
};

export default function Skill() {
    const [skillData, setSkillData] = React.useState([]);

    useEffect(() => {
        fetchTabData().then((res) => {
            setSkillData(res.data);
            console.log("Skill Api" + res.data);
        }).catch((err) => {
            console.log("Error While fetching api" + err);
        })
    }, [])
    return (
        <>
            {skillData.map((skillInfo,index) => (
                <div className='' key={index+1}>{skillInfo.skillTab[0].skills.map((skillDataMap,index) => (
                    <Box sx={{ width: '100%', mb: 2 }} key={index+1}>
                        <LinearProgressWithLabel value={skillDataMap.skillPercentage} sx={{ color: '#fff' }} name={skillDataMap.skillName} />
                    </Box>
                ))}</div>
            ))}
        </>
    );
}
