import React, { useEffect, useState } from 'react'
import { fetchHeaderData } from '../../config/MyService'
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';

export default function TopHeader() {
  const [headerData, setHeaderData] = useState([]);

  useEffect(() => {
    fetchHeaderData().then((res) => {
      setHeaderData(res.data);
    }).catch((err) => {
      console.log("Error While fetching api" + err);
    })
  }, [])
  const imgPath = './src/assets/images/top-headerbg.jpg'
  return (
    <div>
      {headerData.map((headerInfo,index)=>
      (
        <header key={index+1} className='top-header' style={{ backgroundImage: `url(${headerInfo.headerImgPath})` }}>
        </header>
      )
      )}
    </div>
  )
}
