import * as React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { Container } from '@mui/material';
import About from './About';
import Skill from './Skill';
import Projects from './Projects';
import Experience from './Experience';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography component={'div'}>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
    };
}

export default function Tabindex() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Container maxWidth="lg" className='tab-container p-0 '>
            <Box 
                sx={{ flexGrow: 1, bgcolor: '#212428', display: 'flex', height: 'auto' }} className="width-100 p-0 custom-tabs-wrap"
            >
                <Tabs
                    indicatorColor="red"
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    aria-label="Vertical tabs example"
                    sx={{ borderRight: 1, borderColor: '#fff',color:'#fff' }}
                    className="custom-tabs"
                >
                    <Tab label="About" {...a11yProps(0)} sx={{color:'#fff' }}  />
                    <Tab label="Skills" {...a11yProps(1)}  sx={{color:'#fff' }}/>
                    <Tab label="Projects" {...a11yProps(2)} sx={{color:'#fff' }} />
                    <Tab label="experience" {...a11yProps(3)} sx={{color:'#fff' }}/>
                </Tabs>
                <TabPanel value={value} index={0} sx={{color:'#fff'}} className="width-100 p-0 tabpanelcommon">
                   <About></About>
                </TabPanel>
                <TabPanel value={value} index={1} sx={{color:'#fff' }} className="width-100 p-0 tabpanelcommon">
                   <Skill></Skill>
                </TabPanel>
                <TabPanel value={value} index={2} sx={{color:'#fff' }} className="width-100 p-0 tabpanelcommon">
                    <Projects></Projects>
                </TabPanel>
                <TabPanel value={value} index={3} sx={{color:'#fff' }} className="width-100 p-0 tabpanelcommon">
                   <Experience></Experience>
                </TabPanel>
            </Box>
        </Container>
    );
}
