import { Container } from '@mui/material';
import React, { useEffect, useState } from 'react'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { fetchTabData } from '../../config/MyService';
import Images from "../../utils/Images";

// import Button from '@mui/material/Button';


export default function About() {
    const [aboutData, setAboutData] = useState([]);

    useEffect(() => {
        fetchTabData().then((res) => {
            setAboutData(res.data);
            console.log("About Api" + res.data);
        }).catch((err) => {
            console.log("Error While fetching api" + err);
        })
    }, [])
    return (
        <>
            {aboutData.map((aboutInfo, index) => (
                <div key={index + 1}>
                    <Container className="width-100 p-0 mr-0">
                        <Grid container spacing={2}>
                            <Grid item xs={12} lg={6} xl={6}>
                                <Card>
                                    <CardContent sx={{ minHeight: 50 }}>
                                        <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                            Career Objective
                                        </Typography>
                                        <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                            {aboutInfo.aboutTab[0].careerObjective}
                                        </Typography>
                                    </CardContent>
                                </Card>
                                <Card className='mt-20'>
                                    <CardContent sx={{ minHeight: 50 }}>
                                        <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                           Total Year Of Experience
                                        </Typography>
                                        <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                        {aboutInfo.aboutTab[0].totalYearOfExp}
                                        </Typography>
                                    </CardContent>
                                </Card>
                                <Card className='mt-20'>
                                    <CardContent sx={{ minHeight: 35 }} className="download-resume">   
                                    <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                           Download Resume
                                        </Typography> 
                                        <a className="resume-btn" href={Images.RupeshResume} download={'Resume'}>Download</a>        
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12} lg={6} xl={6}>
                                <Card>
                                    <CardContent sx={{ minHeight: 50 }}>
                                        <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                            Area of Knowledge
                                        </Typography>
                                        <div>
                                            <ul className='academia-lists'>
                                                {aboutInfo.aboutTab[0].areaOfKnowledge.map((areaOfKnow, index) => (
                                                    <li key={index + 1}>{areaOfKnow}</li>
                                                ))}
                                            </ul>
                                        </div>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                    </Container>
                    <Container className='mt-20 p-0'>
                        <Grid container spacing={2}>
                            <Grid item xs={12} lg={12} xl={12}>
                                <Card>
                                    <CardContent sx={{ minHeight: 50 }}>
                                        <Typography  component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                          Academics
                                        </Typography>
                                        <div>
                                            <ul className='academia-lists'>
                                                {aboutInfo.aboutTab[0].Academia.map((acdemicsData, index) => (
                                                    <li key={index + 1}>{acdemicsData.value}</li>
                                                ))}
                                            </ul>
                                        </div>
                                    </CardContent>
                                </Card>

                            </Grid>

                        </Grid>
                    </Container>
                    <Container className='mt-20 p-0'>
                        <Grid container spacing={2}>
                            <Grid item xs={12} lg={12} xl={12}>
                                <Card>
                                    <CardContent sx={{ minHeight: 50 }}>
                                        <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                            Address
                                        </Typography>
                                        <Grid container spacing={2}>
                                            <Grid item xs={12} lg={6} xl={6}>
                                                <Card sx={{ padding: 2 }}>
                                                    <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                                        Permananent Address
                                                    </Typography>
                                                    <Typography component={'div'} sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                                                        {aboutInfo.aboutTab[0].address[0].permananent[0].addressLine1},{aboutInfo.aboutTab[0].address[0].permananent[0].addressLine2} <br />
                                                        {aboutInfo.aboutTab[0].address[0].permananent[0].nearBy},{aboutInfo.aboutTab[0].address[0].permananent[0].city} <br />
                                                        {aboutInfo.aboutTab[0].address[0].permananent[0].state},{aboutInfo.aboutTab[0].address[0].permananent[0].country} <br />
                                                        {aboutInfo.aboutTab[0].address[0].permananent[0].pincode}
                                                    </Typography>
                                                </Card>
                                            </Grid>
                                            <Grid item xs={12} lg={6} xl={6}>
                                                <Card sx={{ padding: 2 }}>
                                                    <Typography component={'div'} sx={{ fontSize: 16 }} color="primary" gutterBottom>
                                                        Current Address
                                                    </Typography>
                                                    <Typography component={'div'} sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                                                        {aboutInfo.aboutTab[0].address[0].current[0].addressLine1},{aboutInfo.aboutTab[0].address[0].current[0].addressLine2} <br />
                                                        {aboutInfo.aboutTab[0].address[0].current[0].nearBy},{aboutInfo.aboutTab[0].address[0].current[0].city} <br />
                                                        {aboutInfo.aboutTab[0].address[0].current[0].state},{aboutInfo.aboutTab[0].address[0].current[0].country} <br />
                                                        {aboutInfo.aboutTab[0].address[0].current[0].pincode}
                                                    </Typography>
                                                </Card>
                                            </Grid>
                                        </Grid>


                                    </CardContent>
                                </Card>

                            </Grid>

                        </Grid>
                    </Container>
                </div>
            ))}
        </>
    )
}
