import React, { useEffect, useState } from 'react'
import { fetchHeaderData } from '../../config/MyService'
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import { Box, Typography } from '@mui/material';

export default function TopHeaderInfo() {
    const [headerData, setHeaderData] = useState([]);
    useEffect(() => {
        fetchHeaderData().then((res) => {
            setHeaderData(res.data);
            console.log(res.data);
        }).catch((err) => {
            console.log("Error While fetching api" + err);
        })
    }, [])
    const imgPath = './src/assets/images/top-headerbg.jpg'
    return (
        <div>
            {headerData.map((headerInfo,index) => (
                <Container maxWidth="lg" className='top-sub-header' key={index+1}>
                    <Box sx={{ backgroundColor: 'rgba(0,0,0,0.4)', color: '#fff' }} >
                        <Box className="mlr-40">
                            <Grid container spacing={2}>
                                <Grid item xs={12} lg={6} xl={6}>
                                    <Grid container spacing={2}>
                                        <Grid item xs={12} lg={4} xl={4} className='user-logo-box-wrap'>
                                            <Box className='user-logo-box'>
                                                <img src={headerInfo.userLogo} alt="user Logo" />
                                            </Box>
                                        </Grid>
                                        <Grid item xs={12} lg={8} xl={8}>
                                            <Typography component={'div'} variant="h5" gutterBottom className="top-sub-header__title">
                                                {headerInfo.name}
                                            </Typography>
                                            <Typography component={'div'} variant="h6" gutterBottom className="top-sub-header__subtitle">
                                                {headerInfo.role}
                                            </Typography>
                                            {/* <SocialList></SocialList> */}

                                            <div>
                                            <ul className='top-sub-header__socialList'>
                                                {headerInfo.socialLinks.map((data,index) =>
                                                    data.link !== "" ? <li key={index+1}><a href={data.link} target="_blank"><img src={data.icon} alt={data.name} /></a></li> : ''
                                                )}
                                            </ul>

                                            </div>

                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} lg={6} xl={6}>
                                    <Grid container spacing={1}>
                                        {headerInfo.genralInfo.map((data) => (
                                            <Grid item xs={12} lg={6} xl={6} key={index+1}>
                                                <Typography component={'div'} variant="h6" className="common-title">
                                                    {data.title}
                                                </Typography>
                                                <Typography component={'div'}  variant="h6" gutterBottom className="common-title-name">
                                                    {data.title.toLowerCase() === "email" ? <a className="common-title-name" href={`mailto:${data.titleValue}`}> {data.titleValue}</a> : <div className="common-title-name" > {data.titleValue}</div>}
                                                </Typography>
                                            </Grid>
                                        ))}


                                    </Grid>
                                </Grid>
                            </Grid>
                        </Box>
                    </Box>
                </Container>))}
        </div>
    )
}
