import { Container } from '@mui/material';
import React, { useEffect, useState } from 'react'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { fetchTabData } from '../../config/MyService';

export default function Projects() {
    const [projectData, setProjectData] = useState([]);

    useEffect(() => {
        fetchTabData().then((res) => {
            setProjectData(res.data);
            console.log("About Api" + res.data);
        }).catch((err) => {
            console.log("Error While fetching api" + err);
        })
    }, [])
    return (
        <>
            {projectData.map((projectData, index) => (
                <div key={index + 1}>
                    <div>
                        <Container className="p-0 mr-0">
                            <Grid container spacing={2}>
                                {projectData.Projects.map((proData, index) => (
                                    <Grid key={index+1} item xs={12} lg={6} xl={6}>
                                        <Card className='' >
                                            <CardContent sx={{ minHeight: 190 }}>
                                                {proData.projectName !== '' ? <Typography component={'div'} sx={{ fontSize: 16, fontWeight: 700 }} color="primary" gutterBottom>
                                                    <span className='project-bullet'>Project Name :</span> {proData.projectName}
                                                </Typography> : ''}
                                                {proData.clientName !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Client Name :</span> {proData.clientName}
                                                </Typography> : ''}
                                                {proData.role !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Role : </span>{proData.role}
                                                </Typography> : ''}
                                                {proData.technologyUsed !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Technology : </span>{proData.technologyUsed}
                                                </Typography> : ''}
                                                {proData.workLink !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Work Link </span> <a href={proData.workLink}>{proData.workLink}</a>
                                                </Typography> : ''}
                                                {proData.teamSize !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Team Size : </span>{proData.teamSize}
                                                </Typography> : ''}
                                                {proData.duration !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Durations : </span>{proData.duration}
                                                </Typography> : ''}
                                            </CardContent>
                                        </Card>
                                    </Grid>
                                ))}
                            </Grid>
                        </Container>
                    </div>
                </div>
            ))}
        </>
    )
}
