import { Container } from '@mui/material';
import React, { useEffect, useState } from 'react'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { fetchTabData } from '../../config/MyService';

export default function Experience() {
    const [projectData, setProjectData] = useState([]);

    useEffect(() => {
        fetchTabData().then((res) => {
            setProjectData(res.data);
            console.log("About Api" + res.data);
        }).catch((err) => {
            console.log("Error While fetching api" + err);
        })
    }, [])
    return (
        <>
            {projectData.map((projectData, index) => (
                <div key={index + 1}>
                    <div>
                        <Container className='p-0 mr-0'>
                            <Grid container spacing={2}>
                                {projectData.experience.map((expData, index) => (
                                    <Grid key={index+1} item xs={12} lg={6} xl={6}>
                                        <Card className={'card-bg-repeat card-bg' + ( expData.lastWorkingDate == 'current' ? ' card-activeted ' : '')} >
                                            <CardContent>
                                                {expData.compName !== '' ? <Typography component={'div'} sx={{ fontSize: 16, fontWeight: 700 }} color="primary" gutterBottom>
                                                    <span className='project-bullet'>Organization:</span> {expData.compName}
                                                </Typography> : ''}   
                                                {expData.compLocation !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Location:</span> {expData.compLocation}
                                                </Typography> : ''} 
                                                {expData.position !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Position:</span> {expData.position}
                                                </Typography> : ''}  
                                                {expData.specialization !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Specialization:</span> {expData.specialization}
                                                </Typography> : ''}   
                                                {expData.startDate !== '' ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Start Date:</span> {expData.startDate}
                                                </Typography> : ''}   
                                                
                                                {expData.lastWorkingDate === "current" ? <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Last Working Date :</span> <span className='text-active'>Currently Working</span>
                                                </Typography> : <Typography component={'div'} sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                                                    <span className='project-bullet'>Last Working Date</span> {expData.lastWorkingDate}
                                                </Typography>}                                               
                                            </CardContent>
                                        </Card>
                                    </Grid>
                                ))}
                            </Grid>
                        </Container>
                    </div>
                </div>
            ))}
        </>
    )
}
